import { Component, OnInit } from '@angular/core';
import { Message, ConfirmationService } from "primeng/primeng";
import { tableMessage, CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { Router, ActivatedRoute, ParamMap } from '@angular/router';
import { CusOperationHttpService } from '../../../http/custom-operation/custom-operation.http.service';
import { QueryPlanBean } from './bean/QueryPlan.bean';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
@Component({
	selector: 'app-query-plan',
	templateUrl: './query-plan.component.html',
	styleUrls: ['./query-plan.component.scss'],
	providers: [CusOperationHttpService, Commfunc, ConfirmationService],
})
export class QueryPlanComponent implements OnInit {
	ch: any = CALENDAR_CN;
	startDate: Date = null;
	thruDate: Date = null;
	msgs: Message[] = [];
	treeOrgName: any;
	treeOrgId: any;
	isShowTip: boolean = true;
	orgTreePage: boolean = false;
	custMajorOrgId: string;
	// 表格中显示的信息
	tabMesg: any = tableMessage;
	headerTitle: string;
	showModel: string;
	creater: string;//创建人
	createrName: string;//创建名字
	marketingProgramId: string//营销活动编号
	queryPlanBean: QueryPlanBean = new QueryPlanBean();
	first: number = 0;//分页控制
	totalNum: number;//总页数
	list: any = [];
	status: any = [];//营销方案状态
	//获取所有码值
	code: any;
	marketProgramId: string;//营销方案编号
	updateValue: any = [];
	userInValue: string;
	orgTreeDisplay: boolean = false;
	personPage: boolean = false;
	userId2: string;
	paramObj: any;
	dealPerShow: boolean = false;//选人
	waitSelUserList: any;
	outVal: any;
	paramEnd: any = {};
	num: number = 0;
	showIco: boolean = true;
	postId:string;
	orgId:string;
	constructor(
		public router: Router,
		public routeInfo: ActivatedRoute,
		public cusOperationHttpService: CusOperationHttpService,
		public commfunc: Commfunc,
		public confirmationService: ConfirmationService
	) {
		this.codeValues() //调用方法，获取全部码值
		//获取所需要的码值,中括号中的变量是自己在原来码值表中的变量名！！！！
		this.status = this.code['MarketingStatusPro'];
		this.orgId=JSON.parse(this.commfunc.getSessionData('commonHeader')).orgId;
		this.userId2 = JSON.parse(this.commfunc.getSessionData('commonHeader')).userId;
		this.postId=JSON.parse(this.commfunc.getSessionData('commonHeader')).extParam.postId;
	}

	ngOnInit() {
		this.marketingProgramId = this.routeInfo.snapshot.params['marketingProgramId'];
		this.queryPlanBean.marketingProgramId = this.marketingProgramId;
		this.queryPlanBean.countNum = 10;
		this.queryPlanBean.pageNum = 1;
		this.first = 0;
		this.commfunc.clearSession('flagIndex');
		this.query();
	}
	//查询
	query() {
		this.doQuery();
	}

	doQuery() {
		if (this.startDate != null || this.startDate != undefined) {
			this.queryPlanBean.startDate = this.commfunc.transDateN(this.startDate);
			this.queryPlanBean.thruDate = this.commfunc.transDateN(this.thruDate);
		}
		this.cusOperationHttpService.queryMarketingProgramList(this.queryPlanBean).subscribe(data => {
			if (data.returnCode.code == "success") {
				this.totalNum = data.totalCount;
				this.list = data.returnList;
				if (data.returnList) {
					for (let item of data.returnList) {
						this.marketProgramId = item.marketingProgramId;
					}

				}
				this.msgs = [];
				this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
			} else {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
			}
		}, error => {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
		})
	}
	//开始日期
	showDataStart() {
		if (this.startDate && this.thruDate) {
			if (this.startDate > this.thruDate) {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: '开始日期不能大于结束日期' });
				setTimeout(() => {
					this.startDate = null;
				})
			}
		}
	}
	//结束日期
	showDataEnd() {
		if (this.startDate && this.thruDate) {
			if (this.startDate > this.thruDate) {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: '开始日期不能大于结束日期' });
				setTimeout(() => {
					this.thruDate = null;
				})
			}
		}
	}
	//手动输入日期时，失去焦点时进行判断
	onchange1() {
		this.showDataStart()
	}
	onchange2() {
		this.showDataEnd()
	}

	//点击X，关闭模态框
	onHideClose() {
		document.body.style.overflow = 'auto';
		this.orgTreeDisplay = false;
		this.personPage = false;
	}
	//机构树的调用
	orgTreeShow() {
		document.body.style.overflow = 'hidden';
		this.orgTreeDisplay = !this.orgTreeDisplay;

	}
	treeCall(param) {
		document.body.style.overflow = 'auto';
		this.headerTitle = '机构';
		this.orgTreeDisplay = false;
		this.treeOrgName = param.orgName;
		this.queryPlanBean.orgId = param.orgId;
	}
	//人员模态框
	userTreeClick() {
		this.headerTitle = '人员';
		document.body.style.overflow = 'hidden';
		if (this.queryPlanBean.orgId == '' || this.queryPlanBean.orgId == undefined||this.queryPlanBean.orgId == null) {
			this.userInValue='0000';
		}else{
			this.userInValue = this.queryPlanBean.orgId;
		}
		this.personPage = !this.personPage;

	}
	//人员回调
	perInfor(event) {
		this.personPage = false;
		this.createrName = event[0].tellerName;
		this.queryPlanBean.userId = event[0].tellerId;
		this.creater = event[0].tellerId;
	}

	//分页
	paginate(event) {
		//每页显示的条数
		this.queryPlanBean.countNum = Number(event.rows);
		//当前页
		let currentPage = event.page + 1;
		this.queryPlanBean.pageNum = currentPage;
		this.first = (currentPage - 1) * this.queryPlanBean.countNum;
		this.doQuery();
	}
	//页码重置
	querySecleReset() {
		this.queryPlanBean.countNum = 10;
		this.queryPlanBean.pageNum = 1;
		this.first = 0;
	}

	//重置
	reset() {
		this.treeOrgName = '';
		this.createrName = '';
		this.startDate = null;
		this.thruDate = null;
		this.queryPlanBean = new QueryPlanBean();
		this.querySecleReset();
	}

	//码值
	codeValues() {
		this.commfunc.codeValue();
		let ret = this.commfunc.codeDatas;
		return this.code = ret //code是自己声明的变量
	}


	//修改按钮
	toRevamp(data) {
		this.orgTreePage = true;
		this.updateValue = data;
	}
	//修改的回调
	updateCall(param) {
		this.orgTreePage = param;
		this.doQuery();
	}

	//撤销按钮
	toDelete(item) {
		this.confirmationService.confirm({
			message: '确认要撤销吗?',
			accept: () => {
				let param = {
					marketingProgramId: item.marketingProgramId,
				}
				this.cusOperationHttpService.deletedMarketingProgram(param).subscribe(data => {
					if (data.returnCode.code == 'success') {
						this.msgs = [];
						this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
						this.doQuery();
					} else {
						this.msgs = [];
						this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
					}
				}, error => {
					this.msgs = [];
					this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务错误' });
				})

			}
		});
	}

	//发布按钮
	toIssue(item) {
		this.confirmationService.confirm({
			message: '确定要发布吗?',
			accept: () => {
				this.num = 1;
				let arr = {};
				arr['marketingProgramId'] = item.marketingProgramId
				this.paramObj = {
					bizType: '01',
					bizPkFieldMap: arr,
				}
				let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
				if (s.extParam.specifyNextOpers) {
					delete s.extParam.specifyNextOpers;
					let commonHeader = s;
					this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
				}
				this.waitSelUserList = this.commfunc.query('YXHDFHLC', {postId:this.postId});
				if (!this.waitSelUserList.erro) {
					this.outVal = this.waitSelUserList.waitSelUserList;
					let chooseThis = this.waitSelUserList.display;
					console.log(this.dealPerShow);
					let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
					let a2 = commonHeader.extParam.specifyNextOpers;
					console.log(a2);
					if (chooseThis == true && (a2 == undefined || a2 == '' || a2 == null)) {
						this.dealPerShow = true;
					} else {
						this.approveClick();
					}

				}else{
					this.msgs = [];
					this.msgs.push({ severity: 'error', summary: '提示', detail: this.waitSelUserList.erro });
				}

			}
		});

	}

	//发布审批
	approveClick() {
		this.cusOperationHttpService.custMarketWorkflowUnifiedEntrance(this.paramObj).subscribe(data => {
			if (data.returnCode.code == 'success') {
				this.msgs = [];
				this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
				this.doQuery();
			} else {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
			}
		}, error => {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务错误' });
		})
	}

	//终止按钮
	toTerminate(item) {
		this.confirmationService.confirm({
			message: '确定要终止吗?',
			accept: () => {
				this.num = 2;
				this.paramEnd = {
					marketingProgramId: item.marketingProgramId,
					statusId: item.statusId,
				}
				let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
				if (s.extParam.specifyNextOpers) {
					delete s.extParam.specifyNextOpers;
					let commonHeader = s;
					this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
				}
				this.waitSelUserList = this.commfunc.query('YXHDFHLC', {postId:this.postId});
				if (!this.waitSelUserList.erro) {
					this.outVal = this.waitSelUserList.waitSelUserList;
					let chooseThis = this.waitSelUserList.display;
					console.log(this.dealPerShow);
					let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
					let a2 = commonHeader.extParam.specifyNextOpers;
					console.log(a2);
					if (chooseThis == true && (a2 == undefined || a2 == '' || a2 == null)) {
						this.dealPerShow = true;
					} else {
						this.endClick();
					}
				}else{
					this.msgs = [];
					this.msgs.push({ severity: 'error', summary: '提示', detail: this.waitSelUserList.erro });
				}
			}
		});
	}

	//终止审批
	endClick() {
		this.cusOperationHttpService.terminateMarketingProgram(this.paramEnd).subscribe(data => {
			if (data.returnCode.code == 'success') {
				this.msgs = [];
				this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
				this.doQuery();
			} else {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
			}
		}, error => {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务错误' });
		})
	}

	outDisplayCall(event) {
		console.log(event);
		this.dealPerShow = event;
	}
	outValueCall(event) {
		console.log(event);
		this.outVal = event;
		let ss = '';
		this.outVal.forEach(item => {
			ss += item.tellerId + ','
		});
		let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
		commonHeader.extParam['specifyNextOpers'] = ss;
		console.log(commonHeader);
		this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
		if (this.num == 1) {
			this.approveClick();//创建
		} else if (this.num == 2) {
			this.endClick()
		}

	}
	// outValueCall2 () {
	// 	console.log(event);
	// 	this.outVal = event;
	// 	let ss = '';
	// 	this.outVal.forEach(item => {
	// 		ss += item.tellerId + ','
	// 	});
	// 	let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
	// 	commonHeader.extParam['specifyNextOpers'] = ss;
	// 	console.log(commonHeader);
	// 	this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
	// 	this.endClick();//终止
	// }

	//查看详情按钮
	//点击营销方案名称查看营销方案详情
	toDetail(item) {
		this.router.navigate(['/pages/tzb/custom/marketing/marketing-project/marketing-program-detail', item])
	}

}
