import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, ParamMap } from '@angular/router';
import { TZB_CUS_UPLOAD, TZB_CUS_DOWNLOAD } from 'app/@uisftech/common/service/http.constant';
import { Message, ConfirmationService } from "primeng/primeng";
import { tableMessage } from 'app/@uisftech/common/constant/config';
import { CusOperationHttpService } from '../../../http/custom-operation/custom-operation.http.service';
import { MarketingProgressBean } from './bean/marketingProgram.bean';
import * as _ from 'lodash';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
@Component({
	selector: 'app-marketing-program',
	templateUrl: './marketing-program.component.html',
	styleUrls: ['./marketing-program.component.scss'],
	providers: [CusOperationHttpService, ConfirmationService, Commfunc]
})
export class MarketingProgramComponent implements OnInit {
	marketingProgressBean: MarketingProgressBean = new MarketingProgressBean();
	name: string;
	UPLOAD: string = TZB_CUS_UPLOAD;//导入
	DOWNLOAD: string = TZB_CUS_DOWNLOAD;//下载
	fileName: string;
	filePath: string;
	fileList: any = [];
	files: any = [];
	msgs: Message[] = [];
	tabMesg: string = tableMessage;
	marketingContentFiles: any = [];
	marketingProgramId: string;//营销方案的编号
	//校验
	userform: FormGroup;

	dealPerShow: boolean = false;//选人
	waitSelUserList: any;
	outVal: any;
	postId: string;
	constructor(
		public router: Router,
		public cusOperationHttpService: CusOperationHttpService,
		public route: ActivatedRoute,
		public fb: FormBuilder,
		public confirmationService: ConfirmationService,
		public commfunc: Commfunc,
	) {

	}

	ngOnInit() {
		this.userform = this.fb.group({
			'marketingProgramContent': new FormControl('', [Validators.required, Validators.maxLength(250)]),
			"marketingProgramName": new FormControl('', [Validators.required, Validators.maxLength(120)])
		});
		this.postId = JSON.parse(this.commfunc.getSessionData('commonHeader')).extParam.postId;
	}


	load(event) {
		for (let file of event.files) {
			this.files.push(file);
			this.fileName = file.fileName;
		}
		if (event.xhr.response) { //上传成功 后台返回的 文件路径 和文件名
			if (JSON.parse(event.xhr.response).success == false) {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: JSON.parse(event.xhr.response).errorMsg });
				return
			}else{
				for (let i of JSON.parse(event.xhr.response)) {
					this.marketingContentFiles.push(i);
				}
				this.marketingProgressBean.fileList = _.clone(this.marketingContentFiles);
				this.msgs = [];
				this.msgs.push({ severity: 'success', summary: '提示', detail: '上传成功！' });
			}
		}
	
	}
	//只有点击提交按钮才能进行创建营销活动
	present() {
		//校验的
		for (const i in this.userform.controls) {
			this.userform.controls[i].markAsDirty();
		}
		//处理是否提交
		for (const i in this.userform.controls) {
			if (!this.userform.controls[i].valid) {
				return;
			}
		}

		this.marketingProgressBean.operateType = '2';
		//校验营销活动名称不能为空
		if (!this.marketingProgressBean.marketingProgramName) {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '提示', detail: '营销方案名称不能为空' });
			return;
		}

		this.confirmationService.confirm({
			message: '确定要提交吗?',
			accept: () => {

				let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
				if (s.extParam.specifyNextOpers) {
					delete s.extParam.specifyNextOpers;
					let commonHeader = s;
					this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
				}
				this.waitSelUserList = this.commfunc.query('YXHDFHLC', { postId: this.postId });
				if (!this.waitSelUserList.erro) {
					this.outVal = this.waitSelUserList.waitSelUserList;
					let chooseThis = this.waitSelUserList.display;
					console.log(this.dealPerShow);
					let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
					let a2 = commonHeader.extParam.specifyNextOpers;
					console.log(a2);
					if (chooseThis == true && (a2 == undefined || a2 == '' || a2 == null)) {
						this.dealPerShow = true;
					} else {
						this.approveClick();
					}
				} else {
					this.msgs = [];
					this.msgs.push({ severity: 'error', summary: '提示', detail: this.waitSelUserList.erro });
				}

			}
		});
	}
	approveClick() {
		this.cusOperationHttpService.createMarketingProgram(this.marketingProgressBean).subscribe(data => {
			if (data.returnCode.code == 'success') {
				this.marketingProgramId = data.marketingProgramId;
				this.router.navigate(['/pages/tzb/custom/marketing/marketing-project/query-plan', { marketingProgramId: this.marketingProgramId }]);
				this.msgs = [];
				this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
			} else {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
			}

		}, error => {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });

		})
	}

	outDisplayCall(event) {
		console.log(event);
		this.dealPerShow = event;
	}
	outValueCall(event) {
		console.log(event);
		this.outVal = event;
		let ss = '';
		this.outVal.forEach(item => {
			ss += item.tellerId + ','
		});
		let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
		commonHeader.extParam['specifyNextOpers'] = ss;
		console.log(commonHeader);
		this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
		this.approveClick();
	}
	//点击X，关闭模态框
	onHideClose() {
		document.body.style.overflow = 'auto';
		this.dealPerShow = false;
	}
	//点击保存按钮之后跳转到营销方案查询界面-如果点击的是保存按钮则不可以创建营销活动
	save() {
		//校验的
		for (const i in this.userform.controls) {
			this.userform.controls[i].markAsDirty();
		}
		//处理是否提交
		for (const i in this.userform.controls) {
			if (!this.userform.controls[i].valid) {
				return;
			}
		}

		this.marketingProgressBean.operateType = '1';

		this.cusOperationHttpService.createMarketingProgram(this.marketingProgressBean).subscribe(data => {
			if (data.returnCode.code == 'success') {
				this.marketingProgramId = data.marketingProgramId;
				this.router.navigate(['/pages/tzb/custom/marketing/marketing-project/query-plan', { marketingProgramId: this.marketingProgramId }]);
				this.msgs = [];
				this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
			} else {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
			}

		}, error => {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });

		})
	}

	//下载按钮
	down(temp) {
		let name = temp.fileName;
		let path = temp.fileUrl;
		open(decodeURI(TZB_CUS_DOWNLOAD + "?fileName=" + name + "&fileUrl=" + path));
	}
	toShanChu(i) {
		this.marketingProgressBean.fileList.splice(i, 1)
		this.marketingProgressBean.fileList = _.clone(this.marketingProgressBean.fileList);

	}
}
