import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';
import { MarketingProgramComponent } from './marketing-program/marketing-program.component';
import { MarketingProgramDetail } from './marketing-program-detail/marketing-program-detail.component';
import { QueryPlanComponent } from './query-plan/query-plan.component';
export const routes: Routes = [
    {
        path: '',
        children: [
            {
                path: 'marketing-program',
                component: MarketingProgramComponent
            },
            {
                path: 'marketing-program-detail',
                component: MarketingProgramDetail
            },
            {
                path: 'query-plan',
                component: QueryPlanComponent
            }

        ]
    }
]
export const MarketingProjectRouting: ModuleWithProviders = RouterModule.forChild(routes);