import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Router, ActivatedRoute, ParamMap } from '@angular/router';
import { TZB_CUS_UPLOAD, TZB_CUS_DOWNLOAD } from 'app/@uisftech/common/service/http.constant';
import { Message } from "primeng/primeng";
import { tableMessage } from 'app/@uisftech/common/constant/config';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { CusOperationHttpService } from '../../../http/custom-operation/custom-operation.http.service';
import { UpdateQueryPlanBean } from './bean/UpdateQuery.bean';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import * as _ from 'lodash';
/**
 * 营销方案的修改界面
 */
@Component({
	selector: 'update-query-plan',
	templateUrl: './update-query-plan.component.html',
	styleUrls: ['./update-query-plan.component.scss'],
	providers: [CusOperationHttpService, Commfunc]
})
export class UpdateQueryPlanComponent implements OnInit {
	updateQueryPlanBean: UpdateQueryPlanBean = new UpdateQueryPlanBean();
	@Input() inValue: any;
	@Output() outValue: EventEmitter<any> = new EventEmitter();
	name: string;
	UPLOAD: string = TZB_CUS_UPLOAD;//导入
	DOWNLOAD: string = TZB_CUS_DOWNLOAD;//下载
	fileName: string;
	filePath: string;
	fileList: any = [];
	files: any = [];
	msgs: Message[] = [];
	tabMesg: string = tableMessage;
	marketingContentFiles: any = [];
	marketingProgramId: string;//营销方案的编号
	//校验
	userform: FormGroup;
	selectParentData: any;
	status: any;
	//获取所有码值
	code: any;
	constructor(
		public router: Router,
		public cusOperationHttpService: CusOperationHttpService,
		public route: ActivatedRoute,
		public commfunc: Commfunc,
		public fb: FormBuilder,
	) {
		this.userform = this.fb.group({
			'marketingProgramContent': new FormControl('', [Validators.required, Validators.maxLength(250)]),
		});
	}

	ngOnInit() {
		this.codeValues() //调用方法，获取全部码值
		//获取所需要的码值,中括号中的变量是自己在原来码值表中的变量名！！！！
		this.status = this.code['MarketingStatusPro'];
		// this.marketingProgramId=this.inValue.marketingProgramId;

	}
	ngOnChanges() {
		//接受父页面传过来的数据(字段组id--contentPurposeTypeId)
		// this.selectParentData = this.inValue;
		this.updateQueryPlanBean = this.inValue;
		let arr = [];
		if (this.inValue.fileList != null) {
			for (let item of this.inValue.fileList) {
				let array = {};
				array['fileName'] = item.fileName;
				array['fileUrl'] = item.fileUrl;
				arr.push(array);
			}
			this.marketingContentFiles=arr;
			this.updateQueryPlanBean.fileList = this.marketingContentFiles;
		} else {
			this.updateQueryPlanBean.fileList = [];
		}
	}
	//码值
	codeValues() {
		this.commfunc.codeValue();
		let ret = this.commfunc.codeDatas;
		return this.code = ret //code是自己声明的变量
	}
	load(event) {
		for (let file of event.files) {
			this.files.push(file);
			this.fileName = file.fileName;
		}
		if (event.xhr.response) { //上传成功 后台返回的 文件路径 和文件名
			if (JSON.parse(event.xhr.response).success == false) {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: JSON.parse(event.xhr.response).errorMsg });
				return
			}else{
				for (let i of JSON.parse(event.xhr.response)) {
					this.marketingContentFiles.push(i);
				}
				this.updateQueryPlanBean.fileList = _.clone(this.marketingContentFiles);
				this.msgs = [];
				this.msgs.push({ severity: 'success', summary: '提示', detail: '上传成功！' });
			}
		}
	}
	//提交
	modifySaveBtn() {
		//校验的
		for (const i in this.userform.controls) {
			this.userform.controls[i].markAsDirty();
		}
		//处理是否提交
		for (const i in this.userform.controls) {
			if (!this.userform.controls[i].valid) {
				return;
			}
		}
		//提交服务
		this.cusOperationHttpService.updateMarketingProgram(this.updateQueryPlanBean).subscribe(data => {
			if (data.returnCode.code == 'success') {
				this.outValue.emit(false);
				this.msgs = [];
				this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
			} else {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
			}
		}, error => {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败" });
		});
	}
	//取消
	modifyCloseBtn() {
		this.outValue.emit(false);
	}
	//下载按钮
	down(temp) {
		let name = temp.fileName;
		let path = temp.fileUrl;
		open(TZB_CUS_DOWNLOAD + "?fileName=" + encodeURI(name) + "&fileUrl=" + path);
	}
}
