import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, ParamMap } from '@angular/router';
import { CusOperationHttpService } from '../../../http/custom-operation/custom-operation.http.service';
import { Message } from "primeng/primeng";
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { tableMessage } from 'app/@uisftech/common/constant/config';
import { TZB_CUS_DOWNLOAD } from 'app/@uisftech/common/service/http.constant';
@Component({
	selector: 'marketing-program-detail',
	templateUrl: './marketing-program-detail.component.html',
	styleUrls: ['./marketing-program-detail.component.scss'],
	providers: [CusOperationHttpService, Commfunc]
})
export class MarketingProgramDetail implements OnInit {
	DOWNLOAD: string = TZB_CUS_DOWNLOAD;//下载
	fileList: any = [];
	marketingProgramId: string;//营销方案编号
	totalMessage: any = {};//接收所有的信息
	msgs: Message[] = [];
	//获取所有码值
	code: any;
	status: any = [];
	// 表格中显示的信息
	tabMesg: any = tableMessage;
	showFlag:boolean=true;
	taskId:string;
	constructor(
		public routeInfo: ActivatedRoute,
		public cusOperationHttpService: CusOperationHttpService,
		public commfunc: Commfunc,
		public router: Router,
	) {
		this.codeValues() //调用方法，获取全部码值
		//获取所需要的码值,中括号中的变量是自己在原来码值表中的变量名！！！！
		this.status = this.code['MarketingStatus'];
		
	}

	ngOnInit() {
		if (this.routeInfo.snapshot.params['taskId']) {
			this.showFlag=false;
		}else{
			this.showFlag=true;
		}
		this.marketingProgramId = this.routeInfo.snapshot.params['marketingProgramId'];
		this.detailBtn();
	}

	//详情页面
	detailBtn　() {
		let param = {
			marketingProgramId: this.marketingProgramId
		}
		this.cusOperationHttpService.queryMarketingProgramById(param).subscribe(data => {
			if (data.returnCode.code == "success") {
				this.totalMessage = data;
				this.fileList = data.fileList;

			} else {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
			}
		}, error => {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
		})
	}

	//码值
	codeValues() {
		this.commfunc.codeValue();
		let ret = this.commfunc.codeDatas;
		return this.code = ret //code是自己声明的变量
	}
	//点击已关联营销活动跳转到营销活动列表查询界面
	relevance() {
		let param = {
			marketingProgramId: this.marketingProgramId
		}
		window.open(`#/pages/tzb/custom/marketing/marketing-activite/marketing-activity-query;marketingProgramId=${this.marketingProgramId}?flag=${'1'}`, '_blank');
	}
	//下载按钮
	down(temp) {
		let name = temp.fileName;
		let path = temp.fileUrl;
		open(TZB_CUS_DOWNLOAD + "?fileName=" + encodeURI(name) + "&fileUrl=" + path);
	}
	returnBtn () {
		let param ={
			flag:'9'
		}
		this.commfunc.setSessionDataCH('flagIndex', JSON.stringify(param));
		console.log(this.commfunc.getSessionDataCH('flagIndex'))
		setTimeout(() => {
			window.history.go(-1);
		}, 500)
	}
}
